
.App-header {
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgb(27, 44, 70) 6%, rgb(120, 156, 206) 100%);
    font-family: monospace;
    min-height: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(9px + 2vmin);
    font-weight: bolder;
    color: white;
    border-bottom: 2px solid #333333;
    text-shadow: 2px 2px 2px #000000
}
.App-header p {
    font-size: 0.9rem;
    font-weight: lighter;
    opacity: 0.7;
}

#editor-container {
    width: 100%;
    max-width: 700px;
    float: right;
}

#editor {
    font-family: "Lucida Console", "Courier New", monospace;
    font-size: 0.7rem;
    padding: 1.4rem;
    width: 100%;
    height: 72vh;
    background: rgb(48,48,48);
    background: linear-gradient(190deg, rgb(255, 255, 255) 5%, rgb(193, 216, 250) 93%);
    color: #070707;
    border: 2px solid #c0c0c0;
}
#editor:hover {
    background-color: #262626;
}

#preview-container {
    max-width: 700px;
    width: 100%;
    float: left;
}

#preview {
    border: 2px solid lightgray;
    font-size: 0.8rem;
    padding: 1.2rem;
    overflow-y: auto;
    height: 79vh;
}

#preview pre {
    border: 1px solid lightgray;
    padding: 7px;
    background-color: white;
}
#preview td,
#preview th {
    border: 1px solid #224b4b;
    padding:6px;
}
#preview table {
    margin-bottom: 20px;
}
#preview img {
    max-width: 20vh;
    padding: 20px;
}

#button-bar {
    border-radius: 10px;
    border: 1px solid lightgray;
    background: rgb(50, 64, 84);
    height: 60px;
    padding: 5px;
    width:100%;
    text-align: right;
    float: right;
    max-width: 800px;
}

#button-bar button {
    margin: 5px;
}

.sub-heading {
    background: #324054FF;
    border-radius: 17px;
    font-size: 1rem;
    text-align: center;
    color: #f3f3f3;
    padding: 4px;
}